:root {
  --red: #ff3030;
  --blue: #005eff;
  --white: #fff;
  --firework-color: var(--white);
  --firework-shadow: var(--white);
  --fw1-offset: 0px;
  --fw2-offset: 0px;
  --fw3-offset: 0px;
  --explosion-radius: 250px;
}

*, :before, :after {
  box-sizing: border-box;
}

body, html {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

body {
  color: #fff;
  background-color: #0f0f0f;
  background-image: url("page-bg--1800x900.f9fd5988.svg");
  min-height: 100vh;
  font-family: system-ui;
  display: grid;
  position: relative;
}

.start-button {
  opacity: 0;
  font: inherit;
  color: #fff;
  cursor: pointer;
  text-align: center;
  background-color: #0f0f0f12;
  border: 1px solid #ffffff5c;
  border-radius: 30px;
  min-width: 174px;
  max-width: 300px;
  padding: .4em .7em;
  font-size: clamp(1rem, 4vw, 1.3rem);
  transition: all .4s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.start-button:hover {
  background-color: #3405e0;
}

.button-text {
  transition: opacity .4s;
}

.fa-caret-down {
  transition: transform .2s;
}

.fadeIn {
  animation: .4s .2s forwards fadeIn;
}

.rotateCaret {
  transform: rotate(180deg);
}

.shift-down-animation {
  animation: .4s .2s forwards resizeButton, .4s .6s forwards shiftButton;
}

.ui-modal {
  text-align: center;
  background-color: #00122049;
  border: 1px solid #ffffff5c;
  border-radius: 10px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 700px;
  padding: .8em 1em;
  transition: bottom .4s;
  position: absolute;
  bottom: -40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.row {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.row + .row {
  justify-content: center;
  gap: 5rem;
  margin-top: 1.8rem;
}

a {
  color: #fff;
}

.fa-github {
  border-radius: 50%;
  font-size: 2.5rem;
  transition: color .4s;
}

.fa-github:hover, .fa-github:focus {
  color: #ffffff89;
  outline-color: #0000;
}

.modal-reveal {
  bottom: .5rem;
}

.firework-all, .launcher-container {
  grid-template-columns: repeat(3, 1fr);
  place-items: center;
  gap: 3rem;
  display: grid;
}

.launcher {
  z-index: 1;
  opacity: 0;
  background-color: #0000;
  width: 9px;
  min-height: 9px;
  position: relative;
}

.launch-animation {
  animation: 2.6s ease-out infinite launch;
}

.launcher:after {
  z-index: -1;
  content: "";
  background-color: var(--dropShadow-color);
  filter: drop-shadow(0 0 1.2em var(--dropShadow-color));
  box-shadow: 0 1em 1em var(--boxShadow-color);
  width: 3px;
  height: 30px;
  position: absolute;
  top: 0;
  right: 36%;
}

.lchr-1 {
  --offsetTop: var(--fw1-offset);
  --dropShadow-color: none;
  --boxShadow-color: none;
}

.lchr-2 {
  --offsetTop: var(--fw2-offset);
  --dropShadow-color: none;
  --boxShadow-color: none;
  animation-delay: .3s;
}

.lchr-3 {
  --offsetTop: var(--fw3-offset);
  --dropShadow-color: none;
  --boxShadow-color: none;
  animation-delay: .6s;
}

.firework-container {
  filter: blur(.1rem);
  width: 9px;
  min-height: 9px;
  position: relative;
}

.firework-container:first-of-type, .launcher:first-of-type {
  margin-left: 10rem;
}

.launcher:first-of-type {
  align-self: end;
}

.firework-container:nth-of-type(2), .launcher:nth-of-type(2) {
  place-self: end left;
  margin-bottom: 5rem;
  margin-left: 15rem;
}

.launcher:nth-of-type(2) {
  margin-bottom: 0;
}

.firework-container:nth-of-type(3), .launcher:nth-of-type(3) {
  place-self: start left;
  margin-top: 15rem;
  margin-left: 8rem;
}

.launcher:nth-of-type(3) {
  align-self: end;
}

.firework-spark {
  opacity: 0;
  background-color: var(--firework-color);
  filter: drop-shadow(0 0 .4em var(--firework-shadow));
  border-radius: 2px;
  width: 100%;
  min-height: 100%;
  transition: background-color .4s;
  position: absolute;
}

.firework-animation {
  animation: 2.6s ease-in infinite forwards explode;
}

.fw-2 {
  animation-delay: .3s;
}

.fw-3 {
  animation-delay: .5s;
}

@keyframes launch {
  0% {
    opacity: 1;
  }

  15% {
    opacity: 1;
  }

  20% {
    opacity: .8;
  }

  23.5%, 100% {
    opacity: 0;
    transform: translateY(calc(var(--offsetTop)  - 100vh));
  }
}

@keyframes explode {
  0% {
    opacity: 0;
  }

  20% {
    opacity: 0;
    transform: translate(0);
  }

  30% {
    opacity: 1;
  }

  45%, 100% {
    transform: translate(var(--x-direction), var(--y-direction));
    opacity: 0;
  }

  100% {
  }
}

@keyframes shiftButton {
  100% {
    top: 95%;
  }
}

@keyframes resizeButton {
  100% {
    min-width: 0;
  }
}

@keyframes fadeIn {
  100% {
    opacity: 1;
  }
}

.firework-spark:first-of-type {
  --x-direction: calc(var(--explosion-radius) * cos(0deg));
  --y-direction: calc(-1 * var(--explosion-radius) * sin(0deg));
}

